
import * as React from 'react'
import { Link, animateScroll } from 'react-scroll'

const Header = (props: { profile: UserProfile }) => {
  const { profile } = props
  const { fullName, jobPositions, projects, educations, skills } = profile

  const scrollTop = () => {
    animateScroll.scrollToTop({ smooth: true, duration: 200 })
  }

  return (
    <nav className="navbar" style={{ top: 0, position: `fixed`, zIndex: 1000, backgroundColor: `black` }} id="top">
      <div className="navbar__inner">
        <div className="navbar__items">
          <div className="navbar__brand hoverLarge" style={{ marginLeft: 8 }} onClick={scrollTop}>{fullName}</div>
          <div className="navbar__items navbar__items--right">
            {jobPositions && jobPositions.length > 0 &&
              <Link to="experience">
                <span className="navbar__item navbar__link">
                  Experience
                </span>
              </Link>
            }
            {projects && projects.length > 0 &&
              <Link to="projects">
                <span className="navbar__item navbar__link">
                  Projects
                </span>
              </Link>
            }
            {educations && educations.length > 0 &&
              <Link to="education">
                <span className="navbar__item navbar__link">
                  Education
                </span>
              </Link>
            }
            {skills && skills.length > 0 &&
              <Link to="skills">
                <span className="navbar__item navbar__link">
                  Skills
                </span>
              </Link>
            }
          </div>
        </div>
      </div>
    </nav >
  )
}

export default Header